html,body,div,span,object,h1,h2,h3,h4,h5,h6,p,blockquote,a,button,abbr,address,img,q,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,footer,header,section,summary{margin:0;padding:0;border:0;font:inherit}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body,html,#root,.App{width:100%;height:100%;margin:0;padding:0;font-family:"Noto Sans DemLight","맑은 고딕","Malgun Gothic",Dotum,sans-serif;font-size:13px;color:#333;letter-spacing:-0.045em !important}ol,ul{list-style:none}table{border-collapse:collapse;border-spacing:0}img{border:none}a[href^=tel]{color:inherit;text-decoration:none}a{text-decoration:none;color:inherit}address{font-style:normal}button{display:inline-block;border:none;background-color:rgba(0,0,0,0);cursor:pointer}select{appearance:none}select::-ms-expand{display:none}input,button,a,select,option{font-family:inherit;font-size:inherit}input[type=checkbox],input[type=radio]{-webkit-appearance:none;position:absolute;vertical-align:middle}input[type=checkbox]::-ms-check,input[type=radio]::-ms-check{border:solid 1px #fff}input[type=text]::-ms-clear{display:none}caption,hr{overflow:hidden;position:relative;left:0;top:0;width:1px;height:1px;background:0 0;font-size:0;color:rgba(0,0,0,0);line-height:0;text-indent:100%;margin:-1px}pre{word-break:break-all;white-space:pre-line}li{list-style:none}*{-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box}.clearFix:after{content:"";display:block;clear:both}.hidden{display:block;margin:0;padding:0;width:0;height:0;overflow:hidden;font-size:0;line-height:0}#accNav{position:absolute;top:0;left:0;z-index:999999999;width:100%;height:0}#accNav a{display:block;position:absolute;left:0;top:0;overflow:hidden;width:1px;height:1px;margin-left:-1px;margin-bottom:-1px;text-align:center;color:#fff;white-space:nowrap}#accNav a:focus,#accNav a:hover,l #accNav a:active{z-index:1000;width:100%;height:auto;padding:5px 0;background:#444;color:#fff;font-weight:700}.cScrollY{overflow-x:hidden;overflow-y:auto}.cScrollY::-webkit-scrollbar{width:5px}.cScrollY::-webkit-scrollbar-thumb{background:#217af4;border-radius:10px;transition:.3s}.cScrollY::-webkit-scrollbar-track{background:rgba(33,122,244,.1);border-radius:10px}